import * as React from "react";
import * as b from "style/common/TopBar.js";
// Import { useLocation } from "react-router-dom";

export default function TopBar() {
  // Const location = useLocation();
  // console.log(location.pathname);
  // Const [isFolio, setIsFolio] = React.useState();
  const [menuOpen, setMenuOpen] = React.useState(false);

  const toggleMenuHandler = () => {
    setMenuOpen(!menuOpen);
    console.log(menuOpen);
  };

  return (
    <>
      <b.Box>
        <b.TitleBox>
          <b.Title to="/">Malog</b.Title>
          <span
            style={{ fontSize: "1.2rem", fontWeight: "900", color: "#858585" }}
          >
            /
          </span>
          <b.Title to="/folio">folio</b.Title>
        </b.TitleBox>

        <b.NavBox>
          <b.Btn></b.Btn>
          <b.Btn onClick={toggleMenuHandler} style={{ color: "#d66d72" }}>
            
          </b.Btn>
        </b.NavBox>
      </b.Box>
      {menuOpen && <b.Menu />}
    </>
  );
}
